import React, {useEffect, useState} from 'react';
import "../css/styles.css"
import "../css/disclaimer.css"

import {ReactComponent as Check} from "../images/icons/check.svg";

const Disclaimer = () => {
    const [pouzivatelSuhlasi, setPouzivatelSuhlasi] = useState(null);

    useEffect(() => {
        if (pouzivatelSuhlasi) {
            localStorage.setItem("suhlas", "true")
        }
    }, [pouzivatelSuhlasi]);


    useEffect(() => {
        window.scrollTo(0, 0)
        document.title = "Cypo | Disclaimer"
        if (localStorage.getItem("suhlas") === "true") {
            setPouzivatelSuhlasi(true)
        }
    }, []);


    return (
        <div className={"projekt disclaimer"}>
            <div className="disclaimernadpisy">
                <h2>Vyhlsenie o vylen zodpovednosti</h2>
                <h4 className={"nevyrazne"}>Tie znme ako Disclaimer</h4>
            </div>
            <div className="disclaimercontent">
                <div>
                    <h3>1. Vymedzenie zodpovednosti</h3>
                    <p>Obsah zverejnen na tejto strnke sli iba na informan ely a nemal by sa povaova za
                        profesionlnu nhradu</p>
                    <p>Prevdzkovate strnky nezodpoved za akkovek kody alebo straty, ktor by mohli vyplyn z
                        pouvania obsahu zverejnenho na tejto strnke</p>
                </div>
                <div>
                    <h3>2. Obmedzenie zodpovednosti</h3>
                    <p>Pouvatelia pouvaj obsah tejto strnky na vlastn riziko</p>
                    <p>Prevdzkovate strnky nezaruuje presnos, plnos alebo aktulnos poskytovanch informci a
                        nezodpoved za akkovek nepresnosti alebo chyby spsoben v obsahu</p>
                </div>
                <div>
                    <h3>3. Odkazy na tretie strany</h3>
                    <p>Strnka me obsahova odkazy na tretie strany</p>
                    <p>Tieto odkazy s poskytovan iba pre vae pohodlie a prevdzkovate strnky nezodpoved za obsah
                        tchto odkazovanch strnok ani za akkovek kody alebo straty vzniknut pouvanm tchto
                        odkazov</p>
                </div>
                {
                    /*
                    Ochrana osobnch dajov


                    Zber Osobnch dajov: Pri pouvan tejto strnky meme zhromaova osobn daje od pouvateov, ak s dobrovone poskytnut. Tieto daje mu zaha, ale nie s obmedzen na, men, e-mailov adresy, adresy, telefnne sla a alie daje, ktor nm pomhaj s komunikciou s pouvatemi alebo poskytovanm uritch sluieb.

                    Pouitie Osobnch dajov: Osobn daje, ktor zhromaujeme, meme poui na zlepenie naej sluby, sprvu konta pouvatea, zasielanie informci o novinkch, ponukch alebo inch informcich, ktor by mohli by pre pouvatea zaujmav.

                    Ochrana Osobnch dajov: Zavzujeme sa chrni osobn daje pouvateov a prijmame primeran opatrenia na ochranu tchto dajov pred neoprvnenm prstupom, zmenou, zneuitm alebo znienm. Naa prevdzka je v slade so vetkmi platnmi zkonnmi poiadavkami na ochranu osobnch dajov.

                    Zdieanie Osobnch dajov: Osobn daje pouvateov nezdieame, neprename ani nepredvame tretm stranm s vnimkou situci, ke je to nevyhnutn na splnenie prvnych poiadaviek alebo na ochranu naich prv a majetku.

                    Prva Pouvateov: Pouvatelia maj prvo iada prstup k svojim osobnm dajom, ich opravu, obmedzenie spracovania alebo ich vymazanie v prpade, e daje u nie s potrebn na ely, na ktor boli zhromaovan.

                    Aktualizcie Zsad: Tieto Zsady Ochrany Osobnch dajov meme pravidelne aktualizova alebo meni. Ak k takmto zmenm djde, budeme informova pouvateov prostrednctvom e-mailu alebo inch prostriedkov komunikcie.

                    Shlas s Podmienkami: Pouvanm tejto strnky vyjadrujete svoj shlas s tmito Zsadami Ochrany Osobnch dajov. Ak neshlaste s tmito zsadami, prosm, nepokraujte v pouvan tejto strnky.

                    Kontakt na Sprvcu dajov: Ak mte akkovek otzky alebo obavy tkajce sa spracovania vaich osobnch dajov alebo tchto Zsad Ochrany Osobnch dajov, kontaktujte ns na [kontaktn adresa] alebo na [e-mailov adresa].
                    */
                }
                <div>
                    <h3>4. Zmeny vo vyhlsen</h3>
                    <p>Tento dokument o vyhlsen o vylen zodpovednosti me by kedykovek aktualizovan alebo
                        menen bez predchdzajceho upozornenia</p>
                    <p>Aktulna verzia tohto vyhlsenia bude vdy zverejnen na tejto strnke</p>
                </div>
                <div>
                    <h3>5. Kontakt na prevdzkovatea</h3>
                    <p>Ak mte akkovek otzky alebo obavy tkajce sa tohto vyhlsenia alebo obsahu tejto strnky,
                        kontaktujte ma na <a
                            href="mailto:cypooriginal@gmail.com" target={"_blank"}>cypooriginal@gmail.com</a> alebo
                        jednm z <a
                            href="/kontakt">kontaktov</a>
                    </p>
                </div>
            </div>
            <div className="disclaimerciara"></div>
            <div>
                {
                    pouzivatelSuhlasi
                        ? <button
                            style={{
                                display: "flex",
                                alignItems: "center",
                                gap: "1em",
                                }}
                        >
                            Shlasm
                            <Check style={{width: "2em", height: "2em", fill: "var(--colorGood)"}}/>
                        </button>
                        : <button onClick={() => {setPouzivatelSuhlasi(true)}}>Shlasm</button>
                }
            </div>
        </div>
    );
};

export default Disclaimer;
