import React, {useEffect} from 'react';
import {Link} from "react-router-dom";
import "../css/styles.css"
import "../css/stavitelstvo.css"

const Stavitelstvo = () => {

    /* scroll to top */
    useEffect(() => {
        window.scrollTo(0, 0)
        document.title = "Cypo | Stavitestvo"
    }, []);

    return (
        <div className={"stavitelstvo"}>
            <div className="stavitelstvonadpisy">
                <h2>Stavitestvo a stavebn mechanika</h2>
                <h4>Vyber si jeden z nasledujcich projektov</h4>
            </div>
            <div className={"stavitelstvoprojekty"}>
                <Link to={"/schodisko"} className={"stavitelstvoprojekt"}>
                    <h3>Schodisko</h3>
                    <h5>Vpoet potrebnch dajov k navrhnutiu schodiska</h5>
                </Link>
                <Link to={"/zatazenie"} className="stavitelstvoprojekt">
                    <h3>Zaaenie</h3>
                    <h5>Vpoet zaaenia na strop alebo strechu</h5>
                </Link>
                <Link to={"/vystuz"} className="stavitelstvoprojekt">
                    <h3>Vstu</h3>
                    <h5>Nvrh vstue do jednoduchej dosky</h5>
                </Link>
            </div>
        </div>

    );
};

export default Stavitelstvo;
